<template>
  <div id="app">
    <div class="wrap">
      <video-player class="video-player-box" ref="videoPlayer" :options="playerOptions" :playsinline="true" customEventName="customstatechangedeventname" @play="onPlayerPlay($event)" @pause="onPlayerPause($event)" @ended="onPlayerEnded($event)" @statechanged="playerStateChanged($event)" @ready="playerReadied">
      </video-player>
    </div>
    <div>
      <div ref="up" @click="handleUp">上一集</div>
      <div>{{ videoName }}</div>
      <div ref="down" @click="handleDown">下一集</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      count: 0,
      fileLists: [],
      playerOptions: {
        // videojs options
        loop: true,
        muted: true,
        language: 'en',
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [],
        poster: "/static/images/author.jpg",
      },
      videoName: ''
    }
  },
  mounted () {
    this.getFiles()
    console.log('this is current player instance object', this.player)
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  methods: {
    getFiles () {
      const files = require.context('./video/', true, /^./).keys()
      for (let item of files) {
        this.fileLists.push({
          type: item.substring(item.lastIndexOf('.') + 1),
          src: item.substring(2)
        })
      }
      let url = this.fileLists[0].src
      this.videoName = url
      this.$refs.videoPlayer.player.src({ type: 'video/mp4', src: require(`./video/${url}`) })
      this.$refs.videoPlayer.player.play()
    },
    // listen event
    onPlayerPlay (player) {
      // this.count++
      debugger
      // console.log('player play!', player)
    },
    onPlayerPause (player) {
      // console.log('player pause!', player)
    },
    // ...player event

    // or listen state event
    playerStateChanged (playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },

    // player is ready
    playerReadied (player) {
      console.log('the player is readied', player)
      // you can use it to do something...
      // player.[methods]
    },
    onPlayerEnded (e) {
      // this.count++
    },
    handleDown () {
      this.count++
      if (this.count >= this.fileLists.length) {
        this.count = 0
      }
      let type = this.fileLists[this.count].type
      let url = this.fileLists[this.count].src
      this.videoName = url
      this.$refs.videoPlayer.player.src({ type: 'video/mp4', src: require(`./video/${url}`) })
      this.$refs.videoPlayer.player.play()
    },
    handleUp () {
      this.count--
      if (this.count < 0) {
        this.count = 0
      }
      let url = this.fileLists[this.count].src
      this.videoName = url
      this.$refs.videoPlayer.player.src({ type: 'video/mp4', src: require(`./video/${url}`) })
      this.$refs.videoPlayer.player.play()
    }
  }
}
</script>
